<template>
  <div>
    <div class="button-container">
      <el-button v-for="btn in buttons" :key="btn.id" type="primary" @click="handleButtonClick(btn.id)">
        按钮{{ btn.id }}
      </el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ButtonsPage',
  data() {
    return {
      buttons: Array.from({ length: 10 }, (_, i) => ({ id: i + 1 }))
    };
  },
  methods: {
    async handleButtonClick(id) {
      try {
        const response = await axios.get(`http://127.0.0.1:8080/buttonAction/${id}`, {
          headers: {
            'Content-Type': 'application/json'
          }
        });
        this.$message.success(`按钮${id}的操作成功`);
        console.log(response.data);
      } catch (error) {
        this.$message.error(`按钮${id}的操作失败`);
        console.error(error);
      }
    }
  }
};
</script>

<style scoped>
.button-container {
  text-align: center;
  margin-top: 20px;
}
</style>
