import Vue from 'vue';
import Router from 'vue-router';
import OrderPage from '../components/OrderPage.vue';
import ButtonsPage from '../components/ButtonsPage.vue';

Vue.use(Router);

export default new Router({
    mode: 'hash',  // 使用默认的hash模式
    routes: [
        {
            path: '/',
            name: 'OrderPage',
            component: OrderPage
        },
        {
            path: '/buttons',
            name: 'ButtonsPage',
            component: ButtonsPage
        }
    ]
});
